
html,body{
      margin: 0;
      padding: 0;
      font-family: 'Roboto', sans-serif;
}
.title{
      color: #5e47a9;
      padding-left: 10px;
      font-size: 22px;
      margin-bottom: 20px;
}
.itens-container{
      width: 100%;
      height: 200px;
      margin-bottom: 20px;
      overflow-y: hidden;
      overflow-x: scroll;
      -webkit-overflow-scrolling: touch;
}
.item .image img{
      width: 90px;
      border-radius: 100%;
      height: 90px;
}
.item .image{
      width: 90px;
      margin: auto;
}
.item{
      width: 165px;
      float: left;
      height: 200px;
      position: relative;
      padding: 0px 10px;
}
.item::after{
      height: calc(100% - 20px);
      content: " ";
      position: absolute;
      right: 0px;
      background: #f1f1f1;
      width: 1px;
      top: 10px;
}
.item .name{
      margin-bottom: 5px;
      font-size: 18px;
      height: 32px;
      display: -webkit-box;
      line-height: 16px;     /* fallback */
      max-height: 32px;      /* fallback */
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
      overflow:hidden;
      text-overflow: ellipsis;
}
.item .price{
      font-size: 18px;
      margin-top: 7px;
      margin-bottom: 7px;
      color: #36a982;
}
.item .name,.item .description,.item .price{
      text-align: center;
}
.item .description{
      color: #7e7e7e;
      display: -webkit-box;
      line-height: 16px;     /* fallback */
      max-height: 32px;      /* fallback */
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
      overflow:hidden;
      text-overflow: ellipsis;
}
.itens-wrapper{
      /*width: calc((165px + 10px) * 4 - (10px * 4));*/
      float: left;
      height: 215px;
}
.itens-wrapper .item:last-child::after{
      display: none;
}
.featured{
      width: 100%;
      height: 150px;
      margin-top: 20px;
      margin-bottom: 20px;
      overflow-y: hidden;
      overflow-x: scroll;
      -webkit-overflow-scrolling: touch;
}
.wrapper--featured{
      float: left;
      height: 200px;
}
.item--featured{
      /* float: left; */
      display: inline;
      margin-left: 10px;
      /* height: 150px; */
      background-size: cover;
      /* width: 295px; */
      position: relative;
      color: #fefdf9;
      background: red!important;
}
.item--featured .item--provider{
      position: absolute;
      top: 10px;
      left: 10px;
      z-index: 1;
}
.item--featured .item--price{
      color: #ebeb6a;
      position: absolute;
      bottom: 10px;
      left: 10px;
      z-index: 1;
}
.item--featured .item--name{
      position: absolute;
      bottom: 32px;
      left: 10px;
      font-size: 18px;
      max-height: 39px;
      z-index: 1;
      display: -webkit-box;
      line-height: 19px;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
}
.item--featured::after{
      content: ' ';
      width: 100%;
      height: 100%;
      position: absolute;
      bottom: 0px;
      top: 0px;
      display: block;
      background-size: cover;
      background-color: rgba(0, 0, 0, 0.1);
      background-image: url('https://www.ettravel.com/wp-content/uploads/2016/06/GRADIENT-OVERLAY.png')
}
    /**/
.item--featured:last-child{
      margin-right: 10px;
}
.bar-icondominio{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 1;
      width: 100%;
}
.bar-goback{
      position: relative;
      top: 10px;
      height: 34px;
      width: 30px;
      padding-left: 7px;
      float: left;
      left: 10px;
}
.bar-goback::before{
      height: 15px;
      width: 15px;
      margin: 9px -3px;
      position: absolute;
      content: " ";
      display: block;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      border-bottom: 2px solid #482d9e;
      border-left: 2px solid #482d9e;
}
.bar-top{
      height: 50px;
      background-image: url(http://icondominio.com.br/Content/framework/assets/frontend/onepage/img/colored-squares.png);
      background-size: 100%;
      background-position: 0px 0px;
      background-repeat: no-repeat;
}
.bar-title{
      font-size: 20px;
      padding-top: 12px;
      padding-left: 15px;
      float: left;
      color: #482d9e;
}
.menu,.search{
      padding-top: 14px;
      float: right;
}
.bar-title img{
      width: 20px;
      height: 20px;
      top: 3px;
      position: relative;
}
.search i, .menu i {
      font-size: 26px;
      color: #482d9e!important;
}
.search{
      position: relative;
}
.search input{
      opacity: 0;
      position: absolute;
      width: 100%;
      -webkit-transition: opacity 0.5s;
      transition: opacity 0.5s;
}
.search input:focus{
      outline: none;
}
.search-close{
      visibility: hidden;
      opacity: 0;
      position: absolute;
      top: 13px;
      left: 5px;
      z-index: 4;
      color: #482d9e!important;
      -webkit-transition: opacity 0.5s, visibility 0.1s;
      transition: opacity 0.5s, visibility 0.1s;
}
.searching .search input{
      opacity: 1;
      padding-left: 35px;
      top: 5px;
      left: 0;
      right: 0;
      font-size: 26px;
      height: 40px;
      z-index: 3;
      -webkit-appearance: none;
}
.searching .search{
      position: inherit;
}
.search-placeholder{
      width: 0;
      -webkit-transition: width 0.3s;
      transition: width 0.3s;
      height: 50px;
      background: #fafafa;
      position: absolute;
      right: 0px;
      top: 1px;
      z-index: 3;
      -webkit-box-shadow:0 1px 5px 0px #000000bd;
      box-shadow: 0 1px 5px 0px #000000bd;
}
.searching .search-placeholder{
      width: 100%;
}
.searching .search-close {
      opacity: 1;
      visibility: visible;
}
.search-list {
      height: 100%;
      position: absolute;
      top: 50px;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #4527a1;
      /*visibility: hidden;*/
}
.hint-text{
      color: white;
      text-align: center;
      margin-top: 10px;
      font-size: 19px;
}
.group-list{
      padding: 0;
      text-align: center;
}
.group-list li{
      margin-top: 20px;
      display: block;
      color: #fff;
      font-size: 24px;
      padding: 20px;
}



