
.hour-check{
  height: 25px;
  width: 25px;
  border: 1px solid #8a8a8a;
  border-radius: 100%;
  position: absolute;
  left: 5px;
  top: 13px;
}
.noble{
  color: #eaa624!important;
  position: absolute;
  top: 15px;
  right: 5px;
}
.notAvaliable .hour-check{
  border-color: rgba(169, 169, 169, 0.8)
}
.notAvaliable .noble{
  color: rgba(234, 166, 36, 0.8)!important
}
.hour-check i{
  font-size: 17px;
  padding-left: 4px;
  padding-top: 4px;
}
.checked{
  border-color: #4caf50
}
.checked i{
  color: #4caf50!important
}
.hour-box{
  position: relative;
  padding-top: 12px;
  border-bottom: 1px solid darkgrey;
  padding-left: 40px;
  padding-bottom: 20px;
}
.hour-box:last-child{
  border-bottom: 0px solid;
}
.hourTime{
  position: absolute;
  bottom: 6px;
  left: 41px;
  font-size: 12px;
}
.avaliableHours-wrapper{
  margin: auto;
}
.notAvaliable{
  color: rgba(169, 169, 169, 0.8);
}



