
.btn--bottom.btn--absolute {
    bottom: 15px;
}
.ic-space-card {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-top: 15px;
  border-bottom: 1px solid #bec5d6;
  padding-bottom: 10px;
}
.ic-space-card:last-child {
  border: 0px;
}
.ic-spaceTitle{
  font-size: 20px;
    color: #2c5589;
}
.blue .tabs__item--active{
  color: #3b73a4
}
.tabs__slider.blue {
  background: #3b73a4;
}
.btn.blue{
  background-color: #3b73a4 !important;
  border-color: #3b73a4 !important;
  color: white;
}
.tabs__items {
  height: calc(100% - 48px);
  overflow:scroll
}



