
.ic-feature-container{
  margin: 0px 10px;
  padding: 60px 10px 0px 10px;
  background-color: white;
  height: 100%;
  overflow: scroll;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.ic-feature{
  height:100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #f0f3fa;
}
.ic-header-iframe{
  padding-top: 8px;
  padding-bottom: 14px;
  float: left;
  width: 100%;
  position: absolute;
  z-index: 1;
  background-color: #f0f3fa;
}
.ic-header-iframe>.ic-feature-name{
  float: left;
  font-size: 20px;
  padding-top: 12px;
}
.ic-header-iframe>.back{
  width: 23px;
  display: block;
  margin-left: 15px;
  margin-right: 8px;
  float: left;
  margin-top: 10px;
  font-size: 23px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.loading{
  position: absolute;
  top: 0;
}
.ic-bar{
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 2px;
}



