
.ic-space-box {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-top: 15px;
  border-bottom: 1px solid #bec5d6;
  padding-bottom: 10px;
}
.ic-space-box div {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.ic-space-box:last-child {
  border: 0px;
}
.ic-spaceTitle{
  font-size: 20px;
  color: #2c5589;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.avatar {
  background: #dcdcdc;
  font-size: 26px;
  color: #757575;
}



