
.pickerLabel-wrapper{
  width: 262px;
  margin: auto;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.pickerLabel{
  position: relative;
  display: block;
  height: 20px;
}
.pickerLabel > p {
  padding-left: 15px;
  margin: 0;
}
.pickerLabel > .bullet {
  border-radius: 50%;
    top: 5px;
    content: "";
    display: block;
    height: 8px;
    position: absolute;
    width: 8px;
}
.bullet.red { background-color: #f44336 !important;
}
.bullet.yellow { background-color: #ffeb3b !important
}
.btn.green{
  background-color: #4CAF50!important;
  border-color: #4CAF50!important;
  color: white;
}
@-webkit-keyframes indeterminate-short{
0%{left:-200%;right:100%
}
60%{left:107%;right:-8%
}
to{left:107%;right:-8%
}
}
@keyframes indeterminate-short{
0%{left:-200%;right:100%
}
60%{left:107%;right:-8%
}
to{left:107%;right:-8%
}
}
.bar-wrapper{
  width:100%;
  height: 2px;
  background-color: #517095;
  position: relative;
  overflow:hidden;
  margin: 0px;
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}
.noBar {
  opacity: 0;
}
.loading-progress {
     background-color: #cbd7f5;
     opacity: 0.8;
     height: 10px;
     position:absolute;
     width: 50px;
    -webkit-animation: indeterminate-short;
    animation: indeterminate-short;
    -webkit-animation-duration: 2.2s;
    animation-duration: 2.2s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}
.picker__actions.card__actions{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0;
  height: 3px;
}




