
.ic-list-wrapper{
  margin: 0;
  padding: 0;
}
.ic-menu{
  position: absolute;
  top:0;
  left:0;
  bottom:0;
  right: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float: left;
}
.ic-header-fixed{
  position: fixed;
  background: #f4f4f4;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
.ic-header-fixed>.ic-bar{
  width: 100%;
  height: 2px;
  position: absolute;
}
.ic-menu>.ic-header-fixed>.ic-menu-header{
  float: left;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.ic-menu-header>.ic-photo{
  height: 68px;
  width: 68px;
  border-radius: 100%;
  border: 1px solid #e0e0e0;
  padding: 2px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float: left;
}
.ic-menu-header>.ic-photo>img{
  height: 62px;
  width: 62px;
  border-radius: 100%;
}
.ic-menu-header>.ic-icon{
  float: left;
  width: 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 12px;
  font-size: 24px;
}
.ic-menu-header>.ic-user-info{
  float: left;
  width: calc(100% - 40px - 70px);
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.ic-menu-header>.ic-user-info>.ic-hello{
  white-space: nowrap;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 24px;
}
.ic-menu-header>.ic-user-info>.ic-unit{
  color:#898989;
  min-height: 20px;
}
._p-15{
  padding: 15px;
}
.ic-edit-info>.ic-icon{
  width: 25px;
  float: left;
  font-size: 22px;
  margin-right: 15px;
  margin-left: 10px;
}
.ic-edit-info{
  margin-top: 10px;
  float: left;
  color: #636363;
  font-weight: 300;
  font-size: 18px;
}
.ic-menu-body{
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  padding-top: 140px;
  height: 100%;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  background: #fff;
}
.ic-list-link{
  list-style: none;
  margin: 0;
}
.ic-list-link>.ic-link{
  padding: 14px 25px;
  font-size: 18px;
}
.ic-list-link{
  border-bottom: 1px solid #e6e6e6;
}
.ic-list-link:last-child {
  border-bottom: 0px!important;
}
.ic-list-link>.ic-link>.ic-title{
  color: #636363;
  font-weight: 300;
}
.ic-list-link>.ic-link>.ic-icon{
  width: 25px;
  float: left;
  font-size: 23px;
  margin-right: 15px;
}
/* collapse */
.ic-list-link>.expansion-panel{
  -webkit-box-shadow: none;
          box-shadow: none;
}
.expansion-panel__header>.ic-link>.ic-icon{
  width: 25px;
  float: left;
  font-size: 23px;
  margin-right: 15px;
}
.expansion-panel__header>.ic-link>.ic-title{
  color: #636363;
  font-size: 20px;
  font-weight: 300;
}
.expansion-panel__body>.card>.ic-list-link{
  background-color: #f4f4f4;
}
[v-cloak]{display: none;
}
.dropdown {
  position: relative;
  height: 0;
  overflow: hidden;
  -webkit-transition: height 350ms;
  transition: height 350ms;
}
.dropdown::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1rem;
  background-image: -webkit-gradient(linear, left bottom, left top, from(white), to(rgba(255, 255, 255, 0)));
  background-image: linear-gradient(to top, white, rgba(255, 255, 255, 0));
}
.dropdown-enter, .dropdown-leave-to {
  opacity: 0;
}
.dropdown-leave, .dropdown-enter-to {
  opacity: 1;
}
.dropdown-enter-active, .dropdown-leave-active {
  position: absolute;
  width: 100%;
  -webkit-transition: opacity 200ms cubic-bezier(0.4, 0, 0, 1.74);
  transition: opacity 200ms cubic-bezier(0.4, 0, 0, 1.74);
}
.dropdown-enter-active {
  -webkit-transition-delay: 100ms;
          transition-delay: 100ms;
}
.header__icon i{
  display: none;
}
.header__icon::before {
  content: "\e916";
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.expansion-panel__container--active>.expansion-panel__header .header__icon{
  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
}
.transitionSindico,.transitionUnit{
  position: absolute;
  -webkit-animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
  animation: hide-show 10s infinite cubic-bezier(0.4, 0, 0, 1.74);
}
.transitionSindico{
  opacity: 0;
  animation-delay: 5s;
  -webkit-animation-delay: 5s;
}
@-webkit-keyframes hide-show {
0% { opacity: 0;
}
50% { opacity: 1;
}
100% { opacity: 0;
}
}
@keyframes hide-show {
0% { opacity: 0;
}
50% { opacity: 1;
}
100% { opacity: 0;
}
}




